body {
  font-family: sans-serif;
}
.container {
  width: 960px;
  margin: 0 auto;
}
#notes-table,
#notes-stats-table {
  width: 100%;
  text-align: left;
}

#notes-stats-table {
  margin-top: 64px;
}

.table-header {
  background-color: dimgrey;
  height: 36px;
  color: white;
}

#notes-stats-table .table-header tr {
  display: block;
}

.table-header th:first-child {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 8px;
}
.table-header th:last-child {
  border-bottom-right-radius: 8px;
  border-top-right-radius: 4px;
}

.table-header button {
  background: transparent;
  border: none;
}

tbody td {
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
}

#table-name,
#table-created,
#table-category,
#table-content,
#table-dates {
  display: inline-block;
  line-height: 38px;
}

#table-category-icon {
  width: 40px;
}
#table-name {
  width: 150px;
}
#table-created {
  width: 120px;
}
#table-category {
  width: 130px;
}
#table-content {
  width: 220px;
}
#table-dates {
  width: 160px;
}
#table-action-buttons {
  width: 100px;
}
#table-action-buttons button {
  border: none;
  background: transparent;
  cursor: pointer;
  margin: 2px;
  color: dimgrey;
  & svg {
    display: block;
    width: 20px;
    height: 20px;
  }
}

#table-category-icon span,
#table-stats-category-icon span {
  display: block;
  border-radius: 50% 50%;
  background-color: gray;
  padding: 4px;
  margin: 4px;
  width: 24px;
  height: 24px;
}

#table-category-icon span svg,
#table-stats-category-icon span svg {
  width: 20px;
  height: auto;
  margin: auto;
  display: block;
}

#table-stats-category-icon,
#table-stats-category,
#table-stats-active,
#table-stats-archived {
  display: inline-block;
  line-height: 38px;
}

#table-stats-category-icon {
  width: 40px;
}
#table-stats-category {
  width: 350px;
}
#table-stats-active {
  width: 150px;
}
#table-stats-archived {
  width: 80px;
}
#table-stats-archived button {
  cursor: pointer;
  background: transparent;
  border: none;
  font-size: 15px;
  padding: 0;
}

#notes-list,
#notes-stats-list {
  background-color: lightgray;
}

#notes-list tr,
#notes-stats-list tr {
  border-bottom: 4px solid white;
  border-top: 4px solid white;
}

table {
  border-collapse: collapse;
}

.row-end {
  display: flex;
  justify-content: flex-end;
}

#create-note {
  padding: 4px 8px;
}

.modal {
  position: fixed;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  &.open {
    visibility: visible;
    opacity: 1;
    transition-delay: 0s;
  }
  &-bg {
    position: absolute;
    background: lightgray;
    width: 100%;
    height: 100%;
  }
  &-container {
    border-radius: 10px;
    background: #fff;
    position: relative;
    padding: 30px;
  }
  &-close {
    position: absolute;
    right: 15px;
    top: 15px;
    outline: none;
    appearance: none;
    color: red;
    background: none;
    border: 0px;
    font-weight: bold;
    cursor: pointer;
  }

  .modal-container {
    .row {
      display: flex;
      justify-content: space-between;
      margin-bottom: 8px;
    }
    label {
      flex-basis: 33%;
    }
    input,
    select {
      flex-basis: 60%;
      max-width: 60%;
    }
    textarea {
      width: 100%;
    }
  }
}
